body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: linear-gradient(to right, rgb(21, 129, 236), rgb(7, 0, 106));
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.robo_container {
  width: fixed;
  padding: 10px;
  text-align: center;
  background-color: rgb(63, 185, 237);
  display: inline-block;
  margin: 15px;
  border-radius: 20px;

}

.robo_container:hover {
  background-color: rgb(67, 136, 233);
}

.searchboxDiv {
  width: 8vw;
  text-align: center;
  height: 3vw;
  margin-left: 20vw;
  border-radius: 15px;
  background-color: white;
}

.searchboxDiv i {
  width: min-content;
  height: min-content;
  align-content: center;
  background-color: white;
  float: inline-start;
}

.searchboxDiv:focus-within {
  background-color: unset;
}
.searchbox {
  border: none;
  width: 5vw;
  height: 3vw;
  padding: 10px;
  text-align: left;
  place-items: center;
}

.searchbox:focus {
  height: 3vw;
  border: none;
  border-radius: 10px;
  width: 25vw;
  outline: none;
}

h1 {
  text-align: center;
  background-color: rgba(255, 255, 255, 0.95);
  margin: 1.5vw 5vw;
  color: darkblue;
  padding: 30px 0;
  border-radius: 10px;
}

div {
  text-align: center;
}